import http from '@/axios'
import * as constant from '@/constant'
import platBtn from '../assets/image/play-btn.png'

export default {
    data(){
        return {
            trialOver:false,
            trialOverText:constant.PRODUCTION_ENV==0?"请报名参加活动再继续观看":"请使用APP参加活动再继续观看",
            isH5:constant.PRODUCTION_ENV!=0,
            hasStartPlay:false,
            isPlay: false,
            platBtn: platBtn,
            endPlay:true,
        }
    },
    props:['result'],
    computed:{
        activityType(){
            var tmp = "";
            if(this.result.activityForm == 1){
                tmp =  "视频活动";
            }else if(this.result.activityForm == 0){
                tmp =  "线下活动";
            }else if(this.result.activityForm == 2){
                tmp = "问卷活动";
            }

            return tmp;
        },
        online(){
            var tmp = "";
            if(this.result.online == 0){
                tmp = "未开始";
            }else if(this.result.online == 1){
                tmp = "进行中";
            }else if(this.result.online == 2){
                tmp = "已结束";
            }

            return tmp;
        }
    },
    methods:{
        // start() {
        //     this.$refs.video.play();
        // },
        playAll() {
            console.log(this.$refs.video)
            // if(this.$refs.video.webkitRequestFullscreen()) {
            // }

            this.$refs.video.webkitRequestFullscreen()||this.$refs.video.webkitEnterFullscreen();

        },
        clickPlayBtn() {
            var video = this.$refs.video;
            if (this.isPlay) {
                video.pause();
                this.$refs.platBtn.style.opacity = 1;
            } else {
                video.play();
                this.$refs.platBtn.style.opacity = 0;
            }
            this.isPlay = !this.isPlay;
        },
        // isAll() {
        //     var video = this.$refs.videoCanvas;
        //     var ctx = video.getContext('2d');
        //     video.style.width = document.body.clientWidth+'px';
        //     video.style.height = document.body.clientHeight+'px';
        //     video.style.position = 'fixed';
        //     video.style.top = 0;
        //     video.style.left = 0;
        //     video.style.zIndex = 99;
        //     ctx.translate(document.body.clientWidth/2,0);
        //     ctx.rotate(90*Math.PI/180);
        //     // ctx.scale(0.45,1);
        //     console.log(ctx);
        //
        //     let vid = this.$refs.video;
        //     var videoTime = vid.duration; //视频的总时间
        //
        //     var timer = setInterval(()=>{
        //         // alert(video.currentTime);
        //         if(!self.result.join && video.currentTime >= 5){
        //             vid.pause();
        //             video.hasStartPlay = false;
        //             video.trialOver = true;
        //             clearInterval(timer)
        //         }
        //         context.drawImage(video, 0, 0, canvas.width, canvas.height);//绘制视频
        //     },50)
        //
        // },
        playEnd(){
            this.hasStartPlay = false;
           this.$emit("listenToChildEvent",1);
            this.$refs.platBtn.style.opacity = 1;
            this.isPlay = !this.isPlay;
        },
        startPlay(event){
            this.hasStartPlay = true;

            var video = event.currentTarget;
            var self = this;
            var timer = setInterval(()=>{
                // alert(video.currentTime);
                if(!self.result.join && video.currentTime >= 5){
                    video.pause();
                    this.hasStartPlay = false;
                    this.trialOver = true;
                    this.endPlay = false;
                    clearInterval(timer)
                }
            },50)

            // var video = event.currentTarget;
            // var canvas = this.$refs.videoCanvas;
            // var context = canvas.getContext("2d");
            //
            // var videoTime = video.duration; //视频的总时间
            // var self = this;
            //
            // var timer = setInterval(()=>{
            //     // alert(video.currentTime);
            //     if(!self.result.join && video.currentTime >= 5){
            //         video.pause();
            //         this.hasStartPlay = false;
            //         this.trialOver = true;
            //         clearInterval(timer)
            //     }
            //     context.drawImage(video, 0, 0, canvas.width, canvas.height);//绘制视频
            // },50)
        },
        followActivity(){
            http.followActivity(this.result.kid,{follow:this.result.follow?0:1},()=>{
                if(this.result.follow == true){
                    this.result.follow = false
                }else{
                    this.result.follow = true
                }
            })
        }
    },
    mounted(){
        /* 加载视频默认图 */
        var videoImg = new Image();
        videoImg.src = this.result.activityImage1;
        // var canvas = this.$refs.videoCanvas;
        // var context = canvas.getContext("2d");
        // if(videoImg.complete){
        //     context.drawImage(videoImg, 0, 0, canvas.width, canvas.height);//图片
        // }else{
        //     videoImg.onload = () => {
        //         context.drawImage(videoImg, 0, 0, canvas.width, canvas.height);//图片
        //     }
        // }
        /* 加载视频默认图 end */
    },
    beforeDestroy(){
        var video = this.$refs.video;
        video.stop();
    }
}



